import { ViewModelContext } from '../view-model/index';
import { BindingType } from './types';
import { BindingValueConverter } from './binding_value_converter';
import { BindingValueAccessor, BindingValueAccessorFactory } from './binding_value_accessor';
import { ValidationRule, ValidatorFactory } from '../validator/index'

/**
 * 表单控件配置
 */
interface FormControlConfig {
  name: string;
  bindingType: BindingType;
  bindingPath: string;
  valueConverter?: BindingValueConverter;
  valueChanging?: string;
  valueChanged?: string;
  validRules?: ValidationRule | ValidationRule[] | null;
}

/**
 * FormControl定义
 */
class FormControl {

  private valueAccessor: BindingValueAccessor;

  private validatorFn: any;

  private validationResult: any;

  constructor(config: FormControlConfig, viewModelContext: ViewModelContext) {
    this.valueAccessor = BindingValueAccessorFactory.create(
      config.bindingType,
      config.bindingPath,
      config.valueConverter,
      viewModelContext
    );
    this.validatorFn = config.validRules && ValidatorFactory.create(config.validRules)
  }

  public get value() {
    return this.valueAccessor.getValue();
  }

  public set value(val) {
    this.valueAccessor.setValue(val);
  }
}

export { FormControlConfig, FormControl };
