import { Type } from '../core/index';
import { Observable, Subject } from 'rxjs';

import { DataTypeInfo } from '../entity/index';
import { Guid } from '../utils/index';
import { Modification } from '../changeset/index';
import { MetadataUtil } from '../core/index';
import { Entity, createEntity, createEntities, FieldMetadataUtil } from '../entity/index';
import { RepositoryMeta } from './decorators';
import { EntityCollection } from './entity_collection';
import { PaginationManager } from './pagination_manager';
import { DataChangeHistory } from './data_change_history';

export abstract class Repository<T extends Entity> {

  /**
   * 名称
   */
  abstract name: string;

  /**
   * 实体类型
   */
  public entityType: Type<T>;

  /**
   * 实体类型信息
   */
  public entityTypeInfo: DataTypeInfo;

  /**
   * 实体集合
   */
  public entityCollection: EntityCollection<T>;

  /**
   * 用户分页配置信息
   */
  public paginationInfo: any = null;

  /**
   * 数据变更历史
   * @summary
   * 仅针对主表增加、从表删除
   */
  public dataChangeHistory: DataChangeHistory;


  /**
   * 分页管理器
   */
  public paginationManager: PaginationManager<T>;

  /**
   * 构造函数
   */
  constructor() {
  }

  protected init() {
    this.entityTypeInfo = new DataTypeInfo(this.entityType);
    this.entityCollection = new EntityCollection<T>(this.entityType);
    this.dataChangeHistory = new DataChangeHistory();
  }

  /**
   * 实体主键
   */
  public get primaryKey(): string {
    return this.entityCollection.primaryKey;
  }

  /**
   * 实体变更集合
   */
  public get entityCollectionChange(): Subject<Modification> {
    return this.entityCollection.collectionChanged;
  }

  /**
   * 重置状态
   */
  public reset(): void {
    this.entityCollection.clear();
  }

  /**
   * 创建实体
   */
  public buildEntity(data: any): T {
    const entity = createEntity<T>(this.entityType, data);
    return entity;
  }

  /**
   * 批量创建实体
   */
  public buildEntities(listData: any[]): T[] {
    const entities: T[] = createEntities<T>(this.entityType, listData);
    return entities;
  }

  /**
   * 初始化分页配置
   * @param config 用户分页配置
   */
  setPaginationConfig(config: any) {
    this.paginationManager = new PaginationManager(this.entityType, config);
    const { pageSize = 0 } = this.paginationManager.getPaginationConfigByPath('/') || {};
    // tslint:disable-next-line: max-line-length
    this.entityCollection.paginationInfo = Object.assign({ pageSize }, this.entityCollection.paginationInfo, this.paginationManager.pagination);

    // 无需再单独设置一次pageSize，减少一次变更
    // this.entityCollection.pageSize = pageSize;
  }

  /**
   * 设置分页
   */
  public setPaginationInfo(paginationInfo: any) {
    this.paginationInfo = { ...this.paginationInfo, ...paginationInfo };
  }
  
}
