import { MetadataUtil } from '../../core/index';
import {
  PRIMITIVE_PROP_META, OBJECT_PROP_META, DYNAMIC_PROP_META, LIST_PROP_META,
  PrimitivePropMetadata, ObjectPropMetadata, DynamicPropMetadata, ListPropMetadata
} from './field_decorator';

/**
 * 属性注解器通用方法
 */
export class FieldMetadataUtil {
  /**
   * 获取实体所有的简单属性元数据
   * @param target 实体类型
   * @returns 形如：{[propName: string]: NgObjectProperty}
   */
  static getNgFields(target: any): { [propName: string]: PrimitivePropMetadata } {
    return MetadataUtil.getPropsMetadatasByName(target, PRIMITIVE_PROP_META);
  }

  /**
   * 获取某个简单属性的元数据
   */
  static getNgField(target: any, propName: string): PrimitivePropMetadata {
    const ngFields = this.getNgFields(target);
    const ngField = ngFields[propName] as PrimitivePropMetadata;
    return ngField;
  }

  /**
   * 获取实体属性在原始数据中的属性名
   */
  static getDataField(target: any, propName: string) {
    const ngField = this.getNgField(target, propName);
    return ngField.dataField || propName;
  }

  /**
   * 获取标注为NgObject的属性的元数据
   * @param target 实体类型
   * @returns 形如：{[propName: string]: NgObjectProperty}
   */
  static getNgObjects(target: any): { [propName: string]: ObjectPropMetadata } {
    return MetadataUtil.getPropsMetadatasByName(target, OBJECT_PROP_META);
  }

  static getNgDynamic(target: any): { [propName: string]: ObjectPropMetadata } {
    return MetadataUtil.getPropsMetadatasByName(target, DYNAMIC_PROP_META);
  }

  /**
   * 获取标注为NgList的属性的元数据
   * @param target 实体类型
   * @returns 形如：{[propName: string]: NgListProperty}
   */
  static getNgList(target: any): { [propName: string]: ListPropMetadata } {
    return MetadataUtil.getPropsMetadatasByName(target, LIST_PROP_META);
  }

  /**
   * 获取实体标注为主键的属性元数据
   * @param target 实体类型
   */
  static getPrimaryFieldMetadata(target: any): PrimitivePropMetadata | undefined {
    const ngFieldObj = FieldMetadataUtil.getNgFields(target);
    const primaryKey = Object.keys(ngFieldObj).find((prop: string) => {
      return ngFieldObj[prop].primary;
    });

    if (primaryKey) {
      const propMeta = ngFieldObj[primaryKey];
      propMeta.property = primaryKey;
      if (!propMeta.dataField) {
        propMeta.dataField = primaryKey;
      }

      return propMeta;
    }
    return undefined;
  }

  /**
   * 获取主键名称，没有主键时返回空字符串
   */
  static getPrimaryKey(entityType: any) {
    const primaryNgField = this.getPrimaryFieldMetadata(entityType);
    if (!primaryNgField) {
      return '';
    }
    return primaryNgField.property;
  }

}
