import { Type } from '../../core/index';
import { MetadataUtil } from '../../core/index';
import { Entity } from '../entity';

import {
  PropMetadata, PRIMITIVE_PROP_META, PrimitivePropMetadata, OBJECT_PROP_META, ObjectPropMetadata,
  DYNAMIC_PROP_META, DynamicPropMetadata, LIST_PROP_META,    ListPropMetadata
} from './field_decorator';

/**
 * 实体元数据工具类
 */
export class EntityMetadataUtil {

  /**
   * 获取所有属性
   * @todo：封装根据基类获取所有元数据的方法，解决重复代码
   */
  static getAllNgProperties(entityType: Type<Entity>): { [propName: string]: PropMetadata } {
    const ngPlainProperties = this.getNgFieldProperties(entityType);
    const ngEntityProperties = this.getNgObjectProperties(entityType);
    const ngDynamicProperties = this.getNgDynamicProperties(entityType);
    const ngEntityListProperties = this.getNgObjectProperties(entityType);

    return Object.assign({},
      ngPlainProperties,   ngEntityProperties,
      ngDynamicProperties, ngEntityListProperties
    );
  }

  /**
   * 获取NgField的属性元数据
   */
  static getNgFieldProperties(entityType: any): { [propName: string]: PrimitivePropMetadata } {
    return MetadataUtil.getPropsMetadatasByName(entityType, PRIMITIVE_PROP_META);
  }

  /**
   * 获取NgObject属性元数据
   */
  static getNgObjectProperties(entityType: any): { [propName: string]: ObjectPropMetadata } {
    return MetadataUtil.getPropsMetadatasByName(entityType, OBJECT_PROP_META);
  }

  /**
   * 获取NgDynamic属性元数据
   */
  static getNgDynamicProperties(entityType: any): { [propName: string]: DynamicPropMetadata } {
    return MetadataUtil.getPropsMetadatasByName(entityType, DYNAMIC_PROP_META);
  }

  /**
   * 获取NgList属性元数据
   */
  static getNgListProperties(entityType: any): { [propName: string]: ListPropMetadata } {
    return MetadataUtil.getPropsMetadatasByName(entityType, LIST_PROP_META);
  }

  /**
   * 获取主键属性元数据
   */
  static getPrimaryKeyProperty(entityType: Type<Entity>): PrimitivePropMetadata {
    let primaryKeyProperty;
    const ngPlainProperties = EntityMetadataUtil.getNgFieldProperties(entityType);
    Object.keys(ngPlainProperties).forEach((propName: string) => {
      const ngProperty = ngPlainProperties[propName] as PrimitivePropMetadata;
      if (ngProperty.primary === true) {
        primaryKeyProperty = ngProperty;
      }
    });

    return primaryKeyProperty;
  }
}
