import { StaticProvider, Injector } from '../core/index';
import { ViewModelContext } from '../view-model/index';
import { CommandHandlerRegistry } from './command_handler_registry';
import { CommandHandlerExtenderRegistry } from './command_handler_extender_registry';
import { CommandHandlerFactory } from './command_handler_factory';
import { CommandBus } from './command_bus';

const VIEW_MODEL_COMMAND_PROVIDERS: StaticProvider[] = [
  {
    provide: CommandHandlerRegistry,
    useClass: CommandHandlerRegistry,
    deps: [ Injector ]
  },
  {
    provide: CommandHandlerExtenderRegistry,
    useClass: CommandHandlerExtenderRegistry,
    deps: [ Injector ]
  },
  {
    provide: CommandHandlerFactory,
    useClass: CommandHandlerFactory,
    deps: [ CommandHandlerRegistry, CommandHandlerExtenderRegistry, ViewModelContext ]
  },
  {
    provide: CommandBus,
    useClass: CommandBus,
    deps: [ CommandHandlerFactory ]
  }
];

export { VIEW_MODEL_COMMAND_PROVIDERS };
