import { Observable, from, of } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import axios from 'axios';
import { AxiosInstance, AxiosResponse} from 'axios';
import { HttpMethod, HttpRequestConfig } from './types';
import { HttpUtil } from './http-util';

/**
 * HttpClient
 */
class HttpClient {

  /**
   * axios实例
   */
  private axiosInstance: AxiosInstance;

  /**
   * 构造函数
   */
  constructor() {
    this.axiosInstance = axios.create();
  }

  /**
   * 发送GET请求
   */
  public get(url: string, requestConfig: HttpRequestConfig): Observable<any> {
    return this.request('GET', url, requestConfig);
  }

  /**
   * 发送POST请求
   */
  public post(url: string, body: any, requestConfig: HttpRequestConfig) {
    requestConfig = HttpUtil.appendBodyToRequestConfig(body, requestConfig);
    return this.request('POST', url, requestConfig);
  }

  /**
   * 发送PUT请求
   */
  public put(url: string, body: any, requestConfig: HttpRequestConfig) {
    requestConfig = HttpUtil.appendBodyToRequestConfig(body, requestConfig);
    return this.request('PUT', url, requestConfig);
  }

  /**
   * 发送PATCH请求
   */
  public patch(url: string, body: any, requestConfig: HttpRequestConfig) {
    requestConfig = HttpUtil.appendBodyToRequestConfig(body, requestConfig);
    return this.request('PATCH', url, requestConfig);
  }

  /**
   * 发送DELETE请求
   */
  public delete(url: string, requestConfig: HttpRequestConfig) {
    return this.request('DELETE', url, requestConfig);
  }

  /**
   * 发送请求
   */
  request( method: HttpMethod, url: string, requestConfig: HttpRequestConfig): Observable<any> {
    const request$ = of(true).pipe(
      switchMap(() => {
        const axiosRequestConfig = HttpUtil.buildAxiosRequestConfig(method, url, requestConfig);
        return from(this.axiosInstance.request(axiosRequestConfig));
      })
    );
    return request$.pipe(
      map((axiosResponse: AxiosResponse) => {
        const httpResponse = HttpUtil.buildHttpResponse(axiosResponse);
        return requestConfig.observe === 'response' ? httpResponse : axiosResponse.data;
      })
    );
  }

}

export { HttpClient };
