import { MetadataUtil } from '../core/index';
import { UISTATE_PROP_META, UIStatePropMetadata } from './decorators';

export class UIStateMetadataUtil {

  /**
   * 获取NgUIState的属性元数据
   * @param 
   * @returns 属性元数据对象
   * @example
   * 返回格式：
   * {
   *    '属性名称': <NgUIStateProperty>{ ...}
   * }
   */
  static getUIFields(target: Function): {[propName: string]: UIStatePropMetadata} {
      return MetadataUtil.getPropsMetadatasByName(target, UISTATE_PROP_META);
  }
}