/**
 * 变更相关定义
 * @author Witt<jiwt@inspur.com>
 */

/**
 * 绑定数据变更
 */
export interface Change {

  /**
   * 变更类型
   */
  type: ChangeType;

  /**
   * 变更路径
   */
  path: string[];

  /**
   * 变更后的值
   */
  value?: any;

  /**
   * 变更前的值
   */
  preValue?: any;

  /**
   * 错误信息
   */
  errors?: any;

  /**
   * 验证后回调，会将验证结果传入
   */
  cb?: Function;

  /**
   * 是否提交过，为true时开启输入时验证
   */
  isSubmitted?: boolean;

  /**
   * 在grid中使用，如果存在id，则将errors在form中以
   * {
   *  [id]: errors
   * }
   * 的形式保存一份
   */
  id?: string;

  isUdt?: boolean;

  isGrid?: boolean;
}

/**
 * 绑定数据变更类型
 */
export enum ChangeType {
  Load = 'Load',
  Append = 'Append',
  Remove = 'Remove',
  SelectionChanged = 'SelectionChanged',
  ValueChanged = 'ValueChanged',
  UpdateErrors = 'UpdateErrors',
  GlobalSelectionChanged = 'GlobalSelectionChanged',
  /**
   * 分页信息变化
   */
  PaginationInfoChange = 'PaginationInfoChange',
}

/**
 * 视图变更
 */
export interface ViewChange {
  type: ViewChangeType;
  path: string[];
  value: any;
  preValue?: any;
}

/**
 * 视图变更类型
 */
export enum ViewChangeType {
  ValueChanged
}

/**
 * Form值变化事件
 */
export interface FormValueChange {
  path: string[]; // 字段路径
  value: any; // 新值
  preValue?: any; // 旧值
  command: string; // 监听命令
  entityChanged: boolean; // 实体是否已改变
}

/**
 * 实体值变化事件
 */
export interface EntityValueChange {
  paths: string[]; // 字段路径
  value: any; // 新值
  preValue?: any; // 旧值
  changed: boolean; // 实体是否已改变
}