import { ViewModelContext } from '../view-model/index';
import { Command } from './command';

/**
 * Command上下文
 */
class CommandContext {

  /**
   * 命令实例
   */
  command: Command;

  /**
   * 组件上下文
   */
  viewModelContext: ViewModelContext;

  /**
   * 执行结果
   */
  results: {[taskName: string]: any} = {};

  /**
   * 最新的执行结果
   */
  latestResult: any;

  /**
   * 事件参数
   */
  eventParams: any;

  /**
   * 构造函数
   * @param command 命令
   * @param viewModelContext 视图模型上下文
   */
  constructor(command: Command, viewModelContext: ViewModelContext) {
    this.command = command;
    this.viewModelContext = viewModelContext;
  }

}

export { CommandContext };
