/**
 * CommandBus相关定义
 * @author Witt<jiwt@inspur.com>
 */

import { Observable, BehaviorSubject, Subject } from 'rxjs';
import { tap } from 'rxjs/operators';
import { Injector } from '../core/index';
import { Command } from './command';
import { CommandHandlerFactory } from './command_handler_factory';
import { CommandHandlerRegistry } from './command_handler_registry';
import { CommandHandlerExtenderRegistry } from './command_handler_extender_registry';


/**
 * CommandBus用于派发Command，它接受一个Command实例，查找对应的CommandHandler，并执行。
 */
class CommandBus {

  /**
   * handler工厂
   */
  private handlerFactory: CommandHandlerFactory;

  /**
   * 正在执行的命令数量流（请勿使用，后果自负）
   */
  public executingCommandCount$: BehaviorSubject<number>;

  /**
   * 正在执行的命令数量
   */
  private executingCommands: Command[];

  /**
   * 构造函数
   */
  public constructor(handlerFactory: CommandHandlerFactory) {
    this.handlerFactory = handlerFactory;
    this.executingCommands = [];
    this.executingCommandCount$ = new BehaviorSubject<number>(this.executingCommands.length);
  }

  /**
   * 派发命令
   * @param command 要派发的命令
   */
  public dispatch(command: Command): Observable<any> {
    const commandResult$ = new Subject();
    this.executeCommand(command).subscribe({
        next: (lastTaskResult: any) => {
          commandResult$.next(lastTaskResult);
          commandResult$.complete();
        },
        complete: () => {
          commandResult$.complete();
          this.removeCommandFromExecutingQueue(command);
        },
        error: (error: any)  => {
          commandResult$.error(error);
          this.removeCommandFromExecutingQueue(command);
        }
      });
    return commandResult$;
  }

  /**
   * 执行命令并返回最后一个任务的执行结果流
   */
  private executeCommand(command: Command): Observable<any> {
    this.addCommandToExecutingQueue(command);
    const commandName = command.name;
    const handler = this.handlerFactory.create(commandName);
    const lastTaskResult$ = handler.execute(command);
    return lastTaskResult$;
  }


  /**
   * 添加到执行队列
   */
  private addCommandToExecutingQueue(command: Command) {
    this.executingCommands.push(command);
    this.executingCommandCount$.next(this.executingCommands.length);
  }

  /**
   * 从执行队列中移除
   */
  private removeCommandFromExecutingQueue(command: Command) {
    this.executingCommands = this.executingCommands.filter((executingCommand: Command) => {
      return executingCommand !== command;
    });
    this.executingCommandCount$.next(this.executingCommands.length);
  }

}

export { CommandBus };
