/**
 * 数据路径转换、比较等工具类
 * @summary
 * ----------------------------------------
 * 术语：
 * BindingPath：数组形式；
 * BindingPathString：BindingPath的字符串形式，用/分隔；
 * ControlPathString：BindingPath的字符串形式，用.分隔；
 *
 * EntityPath：数组形式；
 * EntityPathString：EntityPath的字符串形式，使用/分隔；
 * ----------------------------------------
 */

/**
 * 路径处理工具类
 * @summary
 * --------------------------------------------------------------------------------
 * 路径现状
 *
 * 字符串格式
 * 1、BindingPath（/edus/grades/id）：主要用于表达式解析；
 * 2、ControlPath（edus.grades.id）： 主要用于FormControl的binding；
 * 3、RestPath（/empId/edus/eduId/grades/gradeId）：主要用于创建Rest
 * 数组格式：
 * 1、BindingPathArray（['edus', 'grades', 'id']）：BindingData模块中使用；
 * 2、EntityPathArray（['id:empId', 'edus', 'id:eduId', 'id']）：Entity模块中使用；
 * --------------------------------------------------------------------------------
 * 转换原则
 * 1、除了该工具类，不允许再出现路径转换、比较的方法；
 * 2、如果有特殊情况，先在该类中实现对应的方法；
 * 1、对于BindingPath、ControlPath统一转换成BingdingPathArray比较
 * 2、对于EntityPath统一转换成EntityPathArray进行比较；
 * --------------------------------------------------------------------------------
 * @todo
 * 1909中使用统一的数据结构（双向链表）进行存储，来解决目前混乱的情况
 */

 export * from './binding_path_converter';
 export * from './binding_path_comparer';
 export * from './binding_path_traverser';
 export * from './entity_path_converter';
 export * from './entity_path_comparer';
 export * from './form_path_converter';
