import { Observable } from 'rxjs';

const isObservable = (value) => {
    if (!value) {
        return false;
    }
    if (value[Symbol.observable] && value === value[Symbol.observable]()) {
        return true;
    }
    if (value['@@observable'] && value === value['@@observable']()) {
        return true;
    }
    if (value instanceof Observable) {
        return true;
    }
    return false;
}
export { isObservable }
