import { Type } from '../../core/types';
import {
  PropMetadata, PrimitivePropMetadata, ObjectPropMetadata, DynamicPropMetadata, ListPropMetadata,
  EntityMetadataUtil
} from '../metadata/index';
import { DataPropGroup, DataPropInfo } from './data_prop_info';

/**
 * 实体类型信息
 * @todo：
 * 1、构造时不应该识别Entity模块的东西，应该是更抽象的；
 * 2、构造函数应该接收一个Builder接口，由Entity或者其他实现层来实现这个接口。
 */
class DataTypeInfo {

  /**
   * 数据类型
   */
  public type: Type<any>;

  /**
   * 属性集合
   */
  public propInfoMap: Map<string, DataPropInfo>;

  /**
   * 主键
   */
  public primaryKey: string;

  /**
   * 外键
   */
  public foreignKey: string;

  /**
   * 是否为值对象
   */
  public get isValueObject() {
    return !this.primaryKey;
  }

  /**
   * 构造函数
   * @todo：不应该识别
   */
  constructor(type: any) {
    this.type = type;
    this.primaryKey = '';
    this.foreignKey = '';
    this.propInfoMap = new Map<string, DataPropInfo>();
    this.collectPropInfos();
  }

  /**
   * 获取全部属性信息
   */
  public getPropInfos(): DataPropInfo[] {
    return Array.from(this.propInfoMap.values());
  }

  /**
   * 获取全部属性的名称
   */
  public getPropNames(): string[] {
    const propNames = [];
    const propInfos = this.getPropInfos();
    propInfos.forEach((propInfo) => {
      propNames.push(propInfo.name);
    });
    return propNames;
  }

  /**
   * 根据group获取属性信息数组
   */
  public getPropInfosByGroup(group: DataPropGroup): DataPropInfo[] {
    const allPropInfos = Array.from(this.propInfoMap.values());
    const propInfos = allPropInfos.filter((propInfo: DataPropInfo) => {
      return propInfo.group === group;
    });
    return propInfos;
  }

  /**
   * 根据group获取属性名称数组
   * @param group 属性分组
   */
  public getPropNamesByGroup(group: DataPropGroup): string[] {
    const propNames = [];
    const propInfos = this.getPropInfosByGroup(group);
    propInfos.forEach((propInfo) => {
      propNames.push(propInfo.name);
    });
    return propNames;
  }

  /**
   * 根据propName获取属性信息
   */
  public getPropInfoByName(propName: string): DataPropInfo {
    if (this.propInfoMap.has(propName)) {
      return this.propInfoMap.get(propName);
    }
    return null;
  }

  /**
   * 根据path获取属性信息
   */
  public getPropInfoByPath(path: string[]): DataPropInfo {

    // 先复制，防止shift方法产生污染
    const arrPath = path.concat([]);
    if (arrPath.length === 0) {
      throw Error(`属性路径不能为空`);
    }

    // 循环查找
    let typeInfo = this;
    let propInfo = null;
    while (typeInfo && arrPath.length > 0) {

      const propName = arrPath.shift();
      propInfo = typeInfo.getPropInfoByName(propName);
      if (!propInfo) {
        throw Error(`路径${path}中存在不正确的节点${propName}，请检查`);
      }
      typeInfo = propInfo.typeInfo;

      // 如果是动态列，并且路径数组里还有属性，统一设置为null(动态列不再描述属性信息)
      if (propInfo.group === DataPropGroup.Dynamic && arrPath.length > 0) {
        propInfo = null;
        typeInfo = null;
      }
    }

    return propInfo;
  }

  /**
   * 根据path获取对应属性的TypeInfo
   */
  public getTypeInfoByPath(path: string[]): DataTypeInfo {

    // 空数组时返回
    if (path.length === 0) {
      return this;
    }

    // 获取对应属性信息
    const propInfo = this.getPropInfoByPath(path);
    if (!propInfo.typeInfo) {
      throw Error(`路径${path}无法定位到一个EntityTypeInfo，请检查`);
    }

    return propInfo.typeInfo;
  }

  /**
   * 获取主键的属性信息
   */
  public getPrimaryKeyPropInfo(): DataPropInfo {
    return this.getPropInfoByName(this.primaryKey);
  }

  /**
   * 根据name获取影射名
   */
  public getPropMappingByName(name: string): string {
    const propInfo = this.getPropInfoByName(name);
    if (!propInfo) {
      return '';
    }
    return propInfo.mapping;
  }

  /**
   * 根据path获取映射名
   */
  public getPropMappingByPath(path: string[]): string {
    const propInfo = this.getPropInfoByPath(path);
    if (!propInfo) {
      return '';
    }
    return propInfo.mapping;
  }

  /**
   * 检查属性是否属于特定的分组
   */
  public checkPropGroup(propName: string, propGroup: DataPropGroup): boolean {
    const propInfo = this.getPropInfoByName(propName);
    if (propInfo && propInfo.group === propGroup) {
      return true;
    }
    return false;
  }


  /**
   * --------------------------------------------------------------------------------
   * 属性元数据 => 属性描述信息
   * --------------------------------------------------------------------------------
   */

  /**
   * 搜集所有属性信息
   * @todo：消除重复代码，ts不支持interface类型检测，暂时通过遍历实现。
   */
  private collectPropInfos() {

    // 简单属性
    const ngPlainProperties = EntityMetadataUtil.getNgFieldProperties(this.type);
    Object.keys(ngPlainProperties).forEach((propName: string) => {
      const ngProperty = ngPlainProperties[propName] as PrimitivePropMetadata;
      if (ngProperty.primary === true) {
        this.primaryKey = propName;
      }
      if (ngProperty.foreign === true) {
        this.foreignKey = propName;
      }
      this.addPropInfo(DataPropGroup.Primitive, propName, ngProperty.dataField, null, ngProperty);
    });

    // 实体属性
    const ngEntityProperties = EntityMetadataUtil.getNgObjectProperties(this.type);
    Object.keys(ngEntityProperties).forEach((propName: string) => {
      const ngProperty = ngEntityProperties[propName] as ObjectPropMetadata;
      this.addPropInfo(DataPropGroup.Object, propName, ngProperty.dataField, ngProperty.type, ngProperty);
    });

    // 动态实体属性
    const ngDynamicProperties = EntityMetadataUtil.getNgDynamicProperties(this.type);
    Object.keys(ngDynamicProperties).forEach((propName: string) => {
      const ngProperty = ngDynamicProperties[propName] as DynamicPropMetadata;
      this.addPropInfo(DataPropGroup.Dynamic, propName, ngProperty.dataField, null, ngProperty);
    });

    // 实体列表属性
    const ngEntityListProperties = EntityMetadataUtil.getNgListProperties(this.type);
    Object.keys(ngEntityListProperties).forEach((propName: string) => {
      const ngProperty = ngEntityListProperties[propName] as ListPropMetadata;
      this.addPropInfo(DataPropGroup.List, propName, ngProperty.dataField, ngProperty.type, ngProperty);
    });
  }

  /**
   * 添加属性信息
   */
  private addPropInfo(group: DataPropGroup, name: string, mapping: string, type: Type<any>, metadataInfo: PropMetadata) {

    // 没有设置影射时，用属性名充当影射
    mapping = mapping ? mapping : name;
    let typeInfo = null;
    if (type) {
      typeInfo = new DataTypeInfo(type);
    }
    const propInfo = { group, name, mapping, typeInfo, metadataInfo };
    this.propInfoMap.set(name, propInfo);
  }

}

export { DataTypeInfo };
