import { makePropDecorator } from '../core/index';
import { CommandParams, ParamDescriptions,  } from '../command/index';

/**
 * 快捷键
 */
export interface Keybinding {
  readonly ctrlKey: boolean;
  readonly shiftKey: boolean;
  readonly altKey: boolean;
  readonly metaKey?: boolean;
  readonly key: string;
}

/**
 * 命令描述
 */
export interface CommandMethodMetadata {

  /**
   * 命令名称
   */
  name: string;

  /**
   * 命令参数
   */
  params?: CommandParams;

  /**
   * 框架id
   * 通过frameId确定命令执行的上下文，不指定则默认为当前Frame。
   */
  frameId?: string;

  /**
   * 参数描述，目前描述类型，后续可兼容精度等其他信息。
   */
  paramDescriptions?: ParamDescriptions;

  /**
   * 快捷键绑定
   */
  keyBinding?: Keybinding;
}


/**
 * 命令装饰器名称
 */
export const COMMAND_METHOD_META = 'CommandMethodMeta';

/**
 * 命令装饰接口
 */
export interface CommandMethodMetaDecorator {
  (ngCommand?: CommandMethodMetadata): any;
  new(ngCommand?: CommandMethodMetadata): any;
}

/**
 * 命令装饰器工厂
 */
export const CommandMethodMeta: CommandMethodMetaDecorator =
  makePropDecorator(COMMAND_METHOD_META, (obj: CommandMethodMetadata) => obj);
