class ArrayUtil {

  /**
   * 从数组中删除一项
   */
  public static remove(arr: any[], itemToRemove: any): void {
    const indexToRemove = arr.findIndex((item: any) => {
      return item === itemToRemove;
    });
    this.removeByIndex(arr, indexToRemove);
  }
  
  /**
   * 从数组中删除indexToRemove对应的项
   * @param index 
   */
  public static removeByIndex(arr: any[], indexToRemove: number) {
    if (!arr || arr[indexToRemove] !== undefined) {
      
    }
    arr.splice(indexToRemove, 1);
  }


}

export { ArrayUtil };