import { DataPathNode, DataPathNodeType } from './data_path_node';
/**
 * 变更路径（简单双向列表）
 */
class DataPath {

  /**
   * 头节点
   */
  public head: DataPathNode;

  /**
   * 长度
   */
  public length: number;

  /**
   * 构造函数
   */
  constructor() {
    this.head = new DataPathNode(null, null);
    this.length = 0;
  }

  /**
   * 添加一个节点到头部
   */
  public unshift(type: DataPathNodeType, data: any) {
    const newNode = new DataPathNode(type, data);
    newNode.next = this.head.next;
    newNode.prev = this.head;

    this.head.next = newNode;
    if (newNode.next) {
      newNode.next.prev = newNode;
    }

    this.length++;
  }

  /**
   * 在链表最后追加一个节点
   */
  public push(type: DataPathNodeType, data: any) {
    const tailNode = this.getTail();
    const newNode = new DataPathNode(type, data);
    tailNode.next = newNode;
    this.length++;
  }

  /**
   * 获取链表尾部节点
   */
  public getTail(): DataPathNode {
    let lastNode = this.head;
    while (lastNode.next) {
      lastNode = lastNode.next;
    }
    return lastNode;
  }

  /**
   * 转换为数组格式
   */
  public toArray() {
    const pathArray = [];
    let currentNode = this.head.next;
    while (currentNode) {
      pathArray.push(`${currentNode.type}:${currentNode.value}`) ;
      currentNode = currentNode.next;
    }
    return pathArray;
  }

  /**
   * 转换为字符串格式
   */
  public toString() {
    const pathArray = this.toArray();
    const pathString = pathArray.join(', ');
    return `[${pathString}]`;
  }

  /**
   * 拷贝
   */
  public clone(): DataPath {
    const newDataPath = new DataPath();
    let curDataNode = this.head.next;
    while (curDataNode) {
      newDataPath.push(curDataNode.type, curDataNode.value);
      curDataNode = curDataNode.next;
    }
    return newDataPath;
  }
}

export { DataPath };
