import { Type, makeDecorator } from '../core/index';

/**
 * REPOSITORY_META
 */
export const REPOSITORY_META = 'RepositoryMeta';


/**
 * RepositoryMeta
 */
export interface RepositoryMeta {

  /**
   * 实体类型
   */
  entityType: Type<any>;
}


/**
 * RepositoryMetaDecorator
 */
export interface RepositoryMetaDecorator {
  (obj?: RepositoryMeta): any;
  new (obj?: RepositoryMeta): any;
}


/**
 * RepositoryMeta
 */
export function RepositoryMeta(options: RepositoryMeta) {
  const decoratorFactory = makeDecorator(
    REPOSITORY_META,
    (obj: RepositoryMeta) => obj
  );
  return decoratorFactory(options);
}
