import { StaticProvider } from '../core/index';
import { VARIABLE_PARSERS } from './variable_parser';
import { DataVariableParser } from './data_variable_parser';
import { UIStateVariableParser } from './ui_state_variable_parser';
import { StateMachineVariableParser } from './state_machine_variable_parser';
import { CommandVariableParser } from './command_variable_parser';
import { VariableParseService } from './variable_parse_service';

const APP_VARIABLE_PROVIDERS: StaticProvider[] = [
  { provide: VARIABLE_PARSERS,     useClass: DataVariableParser,         multi: true, deps: [] },
  { provide: VARIABLE_PARSERS,     useClass: UIStateVariableParser,      multi: true,  deps: [] },
  { provide: VARIABLE_PARSERS,     useClass: StateMachineVariableParser, multi: true,  deps: [] },
  { provide: VARIABLE_PARSERS,     useClass: CommandVariableParser,      multi: true,  deps: [] },
  { provide: VariableParseService, useClass: VariableParseService,       deps: [VARIABLE_PARSERS] },
];

export { APP_VARIABLE_PROVIDERS };
