import { makePropDecorator } from '../core/index';

/**
 * UIState属性元数据接口
 */
export interface UIStatePropMetadata {

  /**
   * 组件的ID
   */
  componentId?: string;

  /**
   * 状态名称
   */
  stateName?: string;
  
}

/**
 * UIState属性元数据名称
 */
export const UISTATE_PROP_META = 'UIStatePropMeta';

/**
 * UIState属性元数据装饰器工厂接口
 */
export interface UIStatePropMetaDecorator {
  (obj?: UIStatePropMetadata): any;
  new(obj?: UIStatePropMetadata): any;
}

/**
 * UIState属性元数据装饰器工厂
 */
export const UIStatePropMeta: UIStatePropMetaDecorator =
  makePropDecorator(UISTATE_PROP_META, (obj?: UIStatePropMetadata) => obj);


