
import { makePropDecorator } from '../core/index';
import { BindingType } from './types';
import { BindingValueConverter } from './binding_value_converter';
import { ValidationRule } from '../validator';

/**
 * FormControl元数据名称
 */
export const FORM_CONTROL_PROP_META = 'FormControlPropMeta';


/**
 * FormControl元数据
 */
export interface FormControlMetadata {

  /**
   * 控件名称
   */
  name?: string;

  /**
   * 绑定字段路径
   */
  bindingType?: BindingType;

  /**
   * 绑定路径
   */
  bindingPath?: string;

  /**
   * 值转换器
   */
  valueConverter?: BindingValueConverter;

    /**
   * 值改变前监听器
   */
  valueChanging?: string;
  /**
   * 值改变后监听器
   */
  valueChanged?: string;
  /**
    * 验证规则
    */
  validRules?: ValidationRule | ValidationRule[] | null;
}

/**
 * 表单控件装饰器
 */
export interface FormControlPropMetaDecorator {
  (obj?: FormControlMetadata): any;
  new(obj?: FormControlMetadata): any;
}

/**
 * FormControl装饰器工厂
 */
export const FormControlPropMeta: FormControlPropMetaDecorator =
  makePropDecorator(FORM_CONTROL_PROP_META, (obj: FormControlMetadata) => obj);
