import { BindingPathConverter } from './binding_path_converter';

/**
 * BindingPath遍历器
 */
class BindingPathTraverser {

  /**
   * 获取叶子节点的Path
   */
  public static getLeafPathString(bindingPath: string | string[]): string {
    const bindingPathArray = BindingPathConverter.toBindingPathArray(bindingPath);
    return bindingPathArray.pop();
  }

  /**
   * 获取父路径
   */
  public static getParentPathString(bindingPath: string | string[]): string {
    const bindingPathArray = BindingPathConverter.toBindingPathArray(bindingPath);
    bindingPathArray.pop();
    return '/' + bindingPathArray.join('/');
  }
}

export { BindingPathTraverser };
