import { TypeDecorator, makeDecorator, makePropDecorator } from '../core/index';

/**
 * ----------------------------------------
 * CommandHandlerMeta
 * ----------------------------------------
 */

/**
 * 命令处理描述
 */
export interface CommandHandlerMetadata {

  /**
   * 要处理的名称
   */
  commandName: string;
}


/**
 * 命令处理装饰器名称
 */
export const COMMAND_HANDLER_META = 'CommandHandlerMeta';


/**
 * 命令处理装饰器接口
 */
export interface CommandHandlerMetaDecorator {
  (handler?: CommandHandlerMetadata): TypeDecorator;
  new(handler?: CommandHandlerMetadata): CommandHandlerMetadata;
}


/**
 * 命令处理装饰器工厂
 */

export function CommandHandlerMeta(options: CommandHandlerMetadata) {
  const decoratorFactory = makeDecorator(COMMAND_HANDLER_META, (handler: CommandHandlerMetadata) => handler);
  return decoratorFactory(options);
}


/**
 * ----------------------------------------
 * CommandExtenderMeta
 * ----------------------------------------
 */

/**
 * 命令处理扩展描述
 */
export interface CommandHandlerExtenderMetadata {
  commandName: string;
}


/**
 * 命令处理扩展装饰器名称
 */
export const COMMAND_HANDLER_EXTENDER_META = 'CommandHandlerExtenderMeta';


/**
 * 命令处理扩展装饰器接口
 */
export interface CommandHandlerExtenderMetaDecorator {
  (extender?: CommandHandlerExtenderMetadata): TypeDecorator;
  new(extender?: CommandHandlerExtenderMetadata): CommandHandlerExtenderMetadata;
}

/**
 * 命令处理扩展装饰器工厂
 */
export function CommandHandlerExtenderMeta(options: CommandHandlerExtenderMetadata) {
  const decoratorFactory = makeDecorator(COMMAND_HANDLER_EXTENDER_META, (extender: CommandHandlerExtenderMetadata) => extender);
  return decoratorFactory(options);
}
