import { makePropDecorator } from '../../core/index';
import { ObjectUtil } from '../../utils/index';
import { ClassType } from '../types';
import { PropMetadata } from './prop_meatadata';


/**
 * 列表属性元数据
 */
export interface ListPropMetadata extends PropMetadata {

  /**
   * 表名
   */
  tableName?: string;

  /**
   * 字段名称
   */
  dataField?: string;

  /**
   * 原始字段名称
   */
  originalDataField?: string;

  /**
   * 实体类型
   */
  type?: any;
}


/**
 * 【列表属性元数据】名称
 */
export const LIST_PROP_META = 'ListPropMeta';


/**
 * 【列表属性元数据装饰器工厂】接口
 */
export interface ListPropMetaDecorator {

  (obj: ListPropMetadata | string | ClassType): any;

  new(obj: ListPropMetadata | string | ClassType): any;

}

/**
 * 【列表属性元数据装饰器工厂】的工厂
 */
function makeListPropMetaDecorator(options: ListPropMetadata | string | ClassType): any {
  if (ObjectUtil.isPlainObject(options)) {
    return options;
  }

  const type = typeof options;
  if (type === 'string') {
    return {
      dataField: options
    };
  }

  if (type === 'function') {
    return {
      type: options
    };
  }
}


/**
 * 列表属性装饰器工厂
 */
export const ListPropMeta: ListPropMetaDecorator = makePropDecorator(LIST_PROP_META, makeListPropMetaDecorator);
