import { Subject, Subscription } from 'rxjs';

class AppEventBus {
  /**
* eventBus
*/
  private eventBus: Subject<any>;

  private subscriptionsMap: Map<string, Subscription>;

  /**
   * 构造函数
   */
  constructor() {
    this.eventBus = new Subject();
    this.subscriptionsMap = new Map<string, Subscription>();
  }

  triggerEvent(event: any) {
    this.eventBus.next(event);
  }

  subscribe(componentId: string, func: Function, funcError?: Function) {
    if (this.subscriptionsMap.has(componentId) === false) {
      const subscription = this.eventBus.subscribe(
        (value) => func(value),
        (value) => funcError(value)
      )
      this.subscriptionsMap.set(componentId, subscription);
    }
  }
  
  unsubscribe(componentId: string) {
    if (this.subscriptionsMap.has(componentId) === true) {
      this.subscriptionsMap.get(componentId).unsubscribe();
      this.subscriptionsMap.delete(componentId);
    }
  }
}

export { AppEventBus };