import { Subject } from 'rxjs';
import { ChangeSet } from '../changeset/change_set';
import { Modification, ModifyType } from '../changeset/types';
import { Entity } from './entity';
import { EntityFactory } from './entity_creator';
import { PARENT_CLASS, PARENT_PATH, ClassType } from './types';

export interface IList<T> {
  [index: number]: T;
}
/**
 * 实体集合列表
 */
export class EntityList<T extends Entity> implements IList<T>, Iterable<T> {

  // #region 私有属性

  /**
   * 已废弃：请勿使用
   */
  private rawData: T[];

  /**
   * 已废弃：请勿使用
   */
  private listChanged = new Subject<Modification>();

  /**
   * 已废弃：请勿使用
   */
  private changeSet = new ChangeSet();
  // #endregion


  // #region 公有属性

  /**
   * 集合改变时触发(新增、行记录修改、删除)
   * @event
   */
  public onListChanged = this.listChanged.asObservable();

  /**
   * 获取项集合
   */
  public get items(): T[] {
    return this.rawData;
  }

  /**
   * 列表变更集
   */
  public get changes() {
    return this.changeSet.changes;
  }

  /**
   * 获取指定索引处的值
   */
  [index: number]: T;

  /**
   * 迭代器
   */
  *[Symbol.iterator](): Iterator<T> {
    yield* this.items;
  }

  // #endregion


  /**
   * @param data JSON数据集合
   * @param type 集合中的实体类型
   */
  constructor(data?: any[], type?: ClassType) {
    this.clear();
    if (data) {
      // this.loadEntities(data);
      data.forEach(item => {
        this.initEntity(EntityFactory(type, item));
      });
    }
  }


  // #region 公有方法

  /** 加载实体列表 */
  public loadEntities(entities: T[]) {
    this.clear();

    entities.forEach(entity => {
      this.initEntity(entity);
    });

    // 发送Load变更
    const changeItem = {
      path: [],
      value: entities,
      preValue: undefined,
      type: ModifyType.Load
    };
    this.setChanges(changeItem);
  }
  /**
   * 清空
   */
  public clear() {
    this.rawData = [];
  }

  /**
   * 添加实体对象到集合中，并返回新加的对象
   * @param entity 实体对象
   */
  public appendNew(entity: T): T {
    const newEntity = this.initEntity(entity);

    // 新增变更
    const changeItem = {
      path: [],
      value: [newEntity],
      preValue: undefined,
      type: ModifyType.Add
    };

    this.setChanges(changeItem);
    return newEntity;
  }

  /**
   * 追加实体
   */
  public appendEntity(entity: T): void {
    const newEntity = this.initEntity(entity);

    // 新增变更
    const changeItem = {
      path: [],
      value: [newEntity],
      preValue: undefined,
      type: ModifyType.Add
    };

    this.setChanges(changeItem);
  }

  /**
   * 批量追加实体
   */
  public appendEntities(entities: T[]): void {
    const newEntites = entities.map((entity: T) => {
      return this.initEntity(entity);
    });
    const changeItem = {
      path: [],
      value: newEntites,
      preValue: undefined,
      type: ModifyType.Add
    };

    this.setChanges(changeItem);
  }

  /**
   * 删除指定主键ID 的实体对象，返回布尔，true 删除成功，false 删除失败
   * @param primaryId 主键ID
   */
  public remove(primaryId: string): boolean {
    const total = this.count();
    const indexToRemove = this.rawData.findIndex((entity: Entity) => {
      return entity.primaryValue === primaryId;
    });
    if (indexToRemove === -1) {
      return false;
    }
    const entityToRemove = this.rawData[indexToRemove];
    this.rawData.splice(indexToRemove, 1);

    // 删除变更
    const changeItem = {
      path: [],
      value: { [entityToRemove.primaryProperty.dataField]: primaryId },
      preValue: undefined,
      type: ModifyType.Remove
    };

    this.updateIndex(total);
    this.setChanges(changeItem);

    return true;
  }

  /**
   * 从集合中获取指定ID值的实体对象
   * @param id 主键值
   */
  public get(id: string) {
    return this.items.find(item => {
      return item.primaryValue === id;
    });
  }

  /**
   * 将变更记录添加到集合变更集中
   * @param value 变更记录
   */
  public setChanges(modinfo: Modification) {
    // 向app层发送的变更
    this.listChanged.next(modinfo);

    // 构造向changeSet中添加的chagne
    const change = Object.assign({}, modinfo);
    if (modinfo.type === ModifyType.Add && modinfo.value[0] instanceof Entity) {
      change.value = [modinfo.value[0].data];
    }
    this.changeSet.append(change);
  }

  /** 集合总记录数 */
  public count() {
    return this.items.length;
  }

  /**
   * 获取实体对象的索引值
   */
  public indexOf(entity: T): number {
    return this.items.indexOf(entity);
  }

  /**
   * 计算集合中某个属性的总和
   * @param propertyName 属性名称
   */
  public sum(propertyName: string): number {
    if (this.count() === 0) {
      return 0;
    }
    return this.items.reduce((val, curr: T) => {
      return val + curr[propertyName];
    }, 0);
  }

  /**
   * 已废弃：请使用toJSON方法代替
   * @deprecated
   */
  public toJson() {
    return this.rawData;
  }

  /**
   * 转换为JSON格式
   */
  public toJSON(): any[] {
    const result = [];
    this.items.forEach((entity: Entity) => {
      result.push(entity.toJSON());
    });
    return result;
  }

  public toArray(): T[] {
    return this.items;
  }

  // #endregion


  // #region 私有方法

  /**
   * 实体初始化
   * @param entity 实体
   */
  private initEntity(entity: T): T {
    entity[PARENT_CLASS] = this;
    entity[PARENT_PATH] = this[PARENT_PATH];
    entity.onValueChanged.subscribe((v: Modification) => {
      const path = v.path;
      const value = v.value;
      const preValue = v.preValue;
      const operator = v.type;
      const subChanges = { path, value, preValue, type: operator };
      this.setChanges(subChanges);
    });
    // TODO: 添加数据验证逻辑代码
    const newLength = this.rawData.push(entity);
    this[newLength - 1] = entity;

    return entity;
  }

  /**
   * 更新索引
   * @param total 总记录数
   */
  private updateIndex(total: number) {
    for (let i = 0; i < total; i++) {
      delete this[i];
    }
    this.rawData.forEach((entity, index) => {
      this[index] = entity;
    });
  }

  /**
   * 获取属性名称
   */
  private getPropertyName() {
    const path = this[PARENT_PATH];
    if (path && path.length) {
      const name = path[path.length - 1];
      return name;
    }
    return undefined;
  }

  // #endregion

}
