import { Injector, InjectFlags } from '../core/index';
import { Context } from '../context/index';
import { AppContext } from '../app/index';
import { Entity } from '../entity/index';
import { Repository } from '../repository/index';
import { BindingData } from '../binding-data/index';
import { UIState } from '../ui-state/index';
import { StateMachine } from '../state-machine/index';
import { Form } from '../form/index';
import { ViewModel } from '../view-model/index';
import { CommandBus } from '../command/index';

/**
 * 视图模型上下文
 */
class ViewModelContext {

  private innerViewModel: ViewModel;

  public appContext: AppContext;

  public root: ViewModelContext;

  public parent: ViewModelContext;

  public id: string;

  constructor() {
  }

  public init(viewModel): void {
    this.innerViewModel = viewModel;
    this.id = this.innerViewModel.id;
    this.appContext = viewModel.injector.get(AppContext);
    this.regToTree();
    this.regToAppContext();
  }

  public regToTree() {
    const parentInjector = this.innerViewModel.injector.get<Injector>(Injector, null, InjectFlags.SkipSelf);
    if (parentInjector)  {
      this.parent = parentInjector.get(ViewModelContext, null);
      this.root = this.parent ? this.parent.root : this;
    } else {
      this.parent = null;
      this.root = this;
    }
  }

  public regToAppContext() {
    this.appContext.regViewModelContext(this);
  }

  public get viewModel(): ViewModel {
    return this.innerViewModel;
  }

  public get injector(): Injector {
    return this.innerViewModel.injector;
  }

  public get repository(): Repository<Entity> {
    return this.innerViewModel.repository;
  }

  public get bindingData(): BindingData {
    return this.innerViewModel.bindingData;
  }

  public get uiState(): UIState {
    return this.innerViewModel.uiState;
  }

  public get stateMachine(): StateMachine {
    return this.innerViewModel.stateMachine;
  }

  public get form(): Form {
    return this.innerViewModel.form;
  }

  public get commandBus(): CommandBus {
    return this.innerViewModel.commandBus;
  }

}

export { ViewModelContext };
