/**
 * 命令处理扩展相关
 * @author Witt<jiwt@inspur.com>
 */
import { createInjectionToken, Injector } from '../core/index';
import { CommandHandler } from './command_handler';


abstract class CommandHandlerExtender {

  /**
   * 扩展方法
   * @param handler 要扩展的命令处理器
   */
  abstract extend(handler: CommandHandler): CommandHandler;

}


/**
 * 命令处理器扩展注入Token
 */
const COMMAND_HANDLER_EXTENDERS_TOKEN = createInjectionToken('@farris/devkit COMMAND_HANDLER_EXTENDERS_TOKEN');

export { CommandHandlerExtender, COMMAND_HANDLER_EXTENDERS_TOKEN };
