import { makePropDecorator } from '../../core/index';
import { ObjectUtil } from '../../utils/index';
import { ClassType } from '../types';
import { PropMetadata } from './prop_meatadata';


/**
 * 【动态对象元数据】接口
 */
export interface DynamicPropMetadata extends PropMetadata {

  /**
   * 映射字段
   */
  dataField?: string;

  /**
   * 原始字段名称
   */
  originalDataField?: string;

  /**
   * 表名
   */
  tableName?: string;

  /**
   * 引用实体类型
   */
  type?: ClassType;
}


/**
 * 【动态对象元数据装饰器】名称
 */
export const DYNAMIC_PROP_META = 'DynamicPropMeta';


/**
 * 【动态对象元数据装饰器工厂】接口
 */
export interface DynamicPropMetaDecorator {

  (obj: DynamicPropMetadata | string | ClassType): any;

  new(obj: DynamicPropMetadata | string | ClassType): any;
}


/**
 * 【动态对象元数据装饰器工厂】的工厂
 */
function makeDynamicPropMetaDecorator(options: DynamicPropMetadata | string | ClassType): any {
  if (ObjectUtil.isPlainObject(options)) {
    return options;
  }

  const type = typeof options;
  if (type === 'string') {
    return {
      dataField: options
    };
  }

  if (type === 'function') {
    return {
      type: options
    };
  }
}


/**
 * 实体属性注解
 */
export const DynamicPropMeta: DynamicPropMetaDecorator = makePropDecorator(DYNAMIC_PROP_META, makeDynamicPropMetaDecorator);