import { DateUtil } from '../utils/index';

/**
 * 绑定值转换器
 */
interface BindingValueConverter {

  /**
   * 将ControlValue转换为StateValue值
   */
  convertFrom(controlValue: any): any;

  /**
   * 将StateValue转换为ControlValue
   */
  convertTo(stateValue: any): any;
}


/**
 * 日期字符串转换器
 */
class DateStringValueConverter implements BindingValueConverter {

  public convertFrom(dateObj: Date): string {
    return  DateUtil.formatISO(dateObj);
  }

  public convertTo(dateString): Date {
    return DateUtil.parse(dateString);
  }
}


/**
 * 数组字符串转换器
 */
class ArrayStringValueConverter implements BindingValueConverter  {

  public convertFrom(arr: any[]): string {
    return  arr.join(',');
  }

  public convertTo(arrString): any[] {
    return arrString.split(',');
  }

}

export { BindingValueConverter, DateStringValueConverter, ArrayStringValueConverter };



