import { makePropDecorator } from '../../core/index';

/**
 * 页面状态元数据
 */
export interface StatePropMetadata {

  /**
   * 初始状态
   */
  initialState?: boolean;
}


/**
 * 状态元数据名称
 */
export const STATE_PROP_META = 'StatePropMeta';


/**
 * 页面状态元数据装饰器工厂接口
 */
export interface StatePropMetaDecorator {
  (obj?: StatePropMetadata): any;
  new(obj?: StatePropMetadata): any;
}

/**
 * 页面状态元数据装饰器工厂
 */
export const StatePropMeta: StatePropMetaDecorator =
  makePropDecorator(STATE_PROP_META, (obj?: StatePropMetadata) => obj);
