import { makePropDecorator } from '../../core/index';

/**
 * 迁移动作
 */
export interface ActionMethodMetadata {
  precondition?: any[];
  transitTo: string;
}


/**
 * 动作方法元数据名称
 */
export const ACTION_METHOD_META = 'ActionMethodMeta';


/**
 * 迁移动作元数据装饰工厂接口
 */
export interface ActionMethodMetaDecorator {
  (obj?: ActionMethodMetadata): any;
  new(obj?: ActionMethodMetadata): any;
}

/**
 * 迁移动作元数据装饰工厂
 */
export const ActionMethodMeta: ActionMethodMetaDecorator =
  makePropDecorator(ACTION_METHOD_META, (action: ActionMethodMetadata) => action);