import { ViewModelContext } from '../view-model/index';

/**
 * ViewModelContext管理类
 */
class ViewModelContextManager {

  /**
   * Context字典
   */
  private contextMap: Map<string, ViewModelContext>;

  /**
   * Context集合
   */
  private contextSet: Set<ViewModelContext>;


  /**
   * 构造函数
   */
  constructor() {
    this.contextMap = new Map<string, ViewModelContext>();
    this.contextSet = new Set<ViewModelContext>();
  }

  /**
   * 注册Context
   */
  public regContext(viewModelContext: ViewModelContext): void {
    const id = viewModelContext.id;
    if (this.contextMap.has(id) === true) {
      // throw Error(`id为${id}的ViewModelContext已经存在`);
      // 临时处理，方式报错。
      this.unregContext(viewModelContext);
    }

    this.contextMap.set(id, viewModelContext);
    this.contextSet.add(viewModelContext);
  }

  /**
   * 取消注册
   */
  public unregContext(context: ViewModelContext): void {
    const id = context.id;
    this.contextMap.delete(id);
    this.contextSet.delete(context);
  }

  /**
   * 获取ContextMap
   */
  public getContextMap() {
    return this.contextMap;
  }

  /**
   * 获取全部Context
   */
  public getContexts(): ViewModelContext[] {
    return Array.from(this.contextSet);
  }

  /**
   * 根据id获取Context
   */
  public getContextById(id: string): ViewModelContext {
    const targetContext = this.contextMap.get(id);
    return targetContext;
  }

  /**
   * 获取根Context
   */
  public getRootContext(): ViewModelContext {
    const contexts = this.getContexts();
    const rootContext = contexts.find((context: ViewModelContext) => {
      return context.parent === null;
    });
    return rootContext;
  }

  /**
   * 获取传入视图模型id的root及root的下一代集合数组
   */
  public getRootContextAndPosterityById(viewModelId): ViewModelContext[] {
    const targetContext = this.getContextById(viewModelId);
    const contexts = this.getContexts();
    let contextsGroup = [];
    // 1.找到ROOT
    const RootId = this.getContextsGroupRoot(targetContext);
    // 2.可直接遍历拿到所有直系后代 (目前只有两层结构直接获取下一代)
    contexts.map((context: ViewModelContext) => {
      if (context.parent && context.parent.id === RootId) {
        contextsGroup.push(context);
      }
    });
    contextsGroup.push(this.getContextById(RootId));
    return contextsGroup;
  }

  private getContextsGroupRoot(context: ViewModelContext) {
    if (context.parent) {
      return this.getContextsGroupRoot(context.parent);
    } else {
      return context.id
    }
  }
}

export { ViewModelContextManager };
