import { makePropDecorator } from '../../core/index';
import { Render } from '../types';

/**
 * 组件状态元数据
 */
export interface RenderStatePropMetadata {
  render: Render;
}


/**
 * 组件状态元数据名称
 */
export const RENDER_STATE_PROP_META = 'RenderStatePropMeta';


/**
 * 组件状态元数据工厂接口
 */
export interface RenderStatePropMetaDecorator {
  (obj?: RenderStatePropMetadata): any;
  new(obj?: RenderStatePropMetadata): any;
}

/**
 * 组件状态元数据工厂
 */
export const RenderStatePropMeta: RenderStatePropMetaDecorator =
  makePropDecorator(RENDER_STATE_PROP_META, (obj: RenderStatePropMetadata) => obj);